import { Component, OnInit } from '@angular/core';
import {PostService} from '../../services/post.service';

@Component({
  selector: 'app-loginregistermodal',
  templateUrl: './loginregistermodal.component.html',
  styleUrls: ['./loginregistermodal.component.css'],
  providers:[PostService]
  
})
export class LoginregistermodalComponent implements OnInit {
  messages:String[];
   fname:String;
   email:String;
   phone:String;
   msg: String;
   success=false;

  constructor(private postService: PostService) { }

  ngOnInit() {
  }
 
 sendMessage(){
   let body={
     fname:this.fname,
     email:this.email,
     phone:this.phone,
     message:this.msg
   }
this.postService.sendMessage(body).subscribe(messages =>{
  this.messages = messages;
   this.success=true;
    this.fname='';
     this.email='';
     this.phone='';
     this.msg='';
  
})
 }


}



