import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {PostService} from '../../services/post.service';
import {PageService} from '../../services/page.service';
import * as moment from 'moment';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css'],
  providers: [PostService,PageService]
})
export class CategoryComponent implements OnInit {
  posts=[];
  pages: Number [];
  baseUrl='/uploads/featured/'

  constructor(private activatedRoute: ActivatedRoute,private cat: PostService,private p:PageService) {
    this.activatedRoute.params.
      map(params => params['name']).subscribe((name)=>{
      this.cat.getCat(name).subscribe(posts => {
      this.posts = posts;
    });
  });




  }

  ngOnInit() {
    this.activatedRoute.params.
      map(params => params['name']).subscribe((name)=>{
      this.p. getPageCat(name).subscribe(list => {

        var l=list/10;
        var pl=Math.round(l);
        var p=[];

        if(pl<20){
          for (var i = 1; i <=pl; i++) {
          p.push(i);
          this.pages = p;
        }
        }
       
    });
    });
  }

   formatDateTime(date: Date): string {
    let d = moment(date);
    var isToday = d.startOf('day').isSame(moment().startOf('day'));
   
    if (!date) {
        return '';
    }
    if(isToday){
     return moment(date).fromNow();
    }
    else{
      return moment(date).format('MMM. D, YYYY [at] h:mm A z');
    }
  }

}



