import { Component, OnInit } from '@angular/core';
import {PostService} from '../../services/post.service';
import {PageService} from '../../services/page.service';
import {ActivatedRoute} from '@angular/router';
import {Router} from '@angular/router';
import * as moment from 'moment';


@Component({
  selector: 'app-body',
  templateUrl: './body.component.html',
  styleUrls: ['./body.component.css'],
  providers: [PostService,PageService]
})
export class BodyComponent implements OnInit {
  slides: String [];
  posts: String [];
  pages: Number [];
  baseUrl='/uploads/featured/'
  /*pages = {
     page:[1,2,3,5,6]
  };*/

  constructor(private activatedRoute: ActivatedRoute,private postService: PostService,private p:PageService ) {
      postService.getSlides().subscribe(slides =>{
      this.slides = slides;
      console.log(slides);
    });

      postService.getPosts().subscribe(posts =>{
      this.posts = posts;
    });

    

    p.getPage().subscribe(list =>{

    var l=list/10;
    var pl=Math.round(l);
    var p=[];

    if(pl<20){
      for (var i = 1; i <=pl; i++) {
      p.push(i);
      this.pages = p;
    }
    }
 

  });


}
  ngOnInit() {

        this.activatedRoute.params.
        map(params => params['page']).subscribe((page)=>{
        this.postService.getPostsbyPage(page).subscribe(posts => {
        this.posts = posts;

      });
     });
  }
  formatDateTime(date: Date): string {
    let d = moment(date);
    var isToday = d.startOf('day').isSame(moment().startOf('day'));
 
    if (!date) {
        return '';
    }
    if(isToday){
     return moment(date).fromNow();
    }
    else{
      return moment(date).format('MMM. D, YYYY [at] h:mm A z');
    }
    
    
}

}



