import { Component, OnInit } from '@angular/core';
import {PostService} from '../../services/post.service';
import {ActivatedRoute} from '@angular/router';
import { Router, NavigationEnd } from '@angular/router';
import * as moment from 'moment';


@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css'],
  providers: [PostService]

})
export class PostComponent implements OnInit {
  baseUrl='/uploads/featured/'
  baseUrl2='/uploads/profiles/'
  post = {
    posted_by:'',
   
  };
  comment: {};
  name: String;
  content: String;
  user = {};
  com = [];
  constructor(private activatedRoute: ActivatedRoute,private singlepostService: PostService, private router: Router) {
    this.activatedRoute.params.
      map(params => params['id']).subscribe((id)=>{
      this.singlepostService.getSingle(id).subscribe(post => {
      this.post = post;
 
    });
  });
  }


  ngOnInit() {
    

    this.router.events.subscribe((evt) => {
        if (!(evt instanceof NavigationEnd)) {
            return;
        }
        this.activatedRoute.params.
          map(params => params['id']).subscribe((id)=>{
          this.singlepostService.countView(id).subscribe(post => {
          this.post = post;

          this.singlepostService.getUser(this.post.posted_by).subscribe(user =>{
          this.user = user;
          
      
   

    });
          
        });

      });
       


    });

  
    
  }

  CommentUpdate(){
    let comment = {
      user:this.name,
      content:this.content
    };
    

    this.activatedRoute.params.
      map(params => params['id']).subscribe((id)=>{
      this.singlepostService.CommentUpdate(id,comment).subscribe(post => {
      this.post = post;
      
      this.name='';
      this.content='';
      this.ngOnInit();
    });
  });
}

 formatDateTime(date: Date): string {
    let d = moment(date);
    var isToday = d.startOf('day').isSame(moment().startOf('day'));
   
    if (!date) {
        return '';
    }
    if(isToday){
     return moment(date).fromNow();
    }
    else{
      return moment(date).format('MMM. D, YYYY [at] h:mm A z');
    }
  }

}



