import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import {PostService} from '../../services/post.service';
import * as moment from 'moment';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css'],
  providers:[PostService]
})
export class FooterComponent implements OnInit {
  latest: String[];
  baseUrl='/uploads/featured/'

  constructor(private router: Router,postService: PostService) {
     postService.getLatest().subscribe(latest =>{
      this.latest = latest;
      console.log(latest)
    });

  }

  ngOnInit() {
  }
  backTop(){
        console.log('fuck');
        window.scrollTo(0, 0);

  }

   formatDateTime(date: Date): string {
    let d = moment(date);
    var isToday = d.startOf('day').isSame(moment().startOf('day'));
 
    if (!date) {
        return '';
    }
    if(isToday){
     return moment(date).fromNow();
    }
    else{
      return moment(date).format('MMM. D, YYYY [at] h:mm A z');
    }
    
    
}

}



