import { Injectable } from '@angular/core';
import {Http,Headers,RequestOptions} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class PostService {

  constructor(private http: Http) {
  }
  getUser(id){
   return this.http.get('/users/user/'+id)
   .map(res => res.json());
 }
 getPosts(){
   return this.http.get('/posts')
   .map(res => res.json());
 }
  getLatest(){
   return this.http.get('/posts/latest')
   .map(res => res.json());
 }

 getSlides(){
   return this.http.get('/posts/slider')
   .map(res => res.json());
 }

 getPostsbyPage(page){
   const p = parseInt(page);
   return this.http.get('/posts/page/'+p)
   .map(res => res.json());
 }
 getPopularPosts(){
   return this.http.get('/posts/popular')
   .map(res => res.json());
 }
 getCat(name){
   return this.http.get('/cats/'+name)
   .map(res=>res.json());
 }
 getSingle(id){
   return this.http.get('/posts/'+id)
   .map(res=>res.json());
 }

  countView(id) {
     let headers = new Headers({ 'Content-Type': 'application/json' });
     let options = new RequestOptions({ headers: headers });
     let body = JSON.stringify(id);
     return this.http.put('/posts/' +id, body, headers).map(res=> res.json());
   }
   CommentUpdate(id,comment) {
     let headers = new Headers({ 'Content-Type': 'application/json' });
     let options = new RequestOptions({ headers: headers });
     let body = JSON.stringify(comment);
     console.log(comment);
     return this.http.put('/comment/'+id, comment, headers).map(res=> res.json());
   }

    sendMessage(body) {
     let headers = new Headers({ 'Content-Type': 'application/json' });
     let options = new RequestOptions({ headers: headers });
     return this.http.post('/messages', body, headers).map(res=> res.json());
   }
}



