import { Component, OnInit } from '@angular/core';
import {PostService} from '../../services/post.service';
import {ActivatedRoute} from '@angular/router';
import * as moment from 'moment';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css'],
  providers:[PostService]
})
export class SidebarComponent implements OnInit {
  baseUrl='/uploads/featured/'
  posts = [];
  constructor(private activatedRoute: ActivatedRoute,private popular: PostService) {
        popular.getPopularPosts().subscribe(posts =>{
        this.posts = posts;
      });
  }

  ngOnInit() {
  }

  

  formatDateTime(date: Date): string {
    let d = moment(date);
    var isToday = d.startOf('day').isSame(moment().startOf('day'));
   
    if (!date) {
        return '';
    }
    if(isToday){
     return moment(date).fromNow();
    }
    else{
      return moment(date).format('MMM. D, YYYY [at] h:mm A z');
    }
  }
}



